import {AdaptPopoverDirective, AdaptPopoverWindow2Component, AdaptPopoverWindowComponent} from './popover.directive';
import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {OverlayModule} from '@angular/cdk/overlay';

@NgModule({
  imports: [CommonModule, OverlayModule],
  declarations: [AdaptPopoverDirective, AdaptPopoverWindowComponent, AdaptPopoverWindow2Component],
  exports: [AdaptPopoverDirective],
  entryComponents: [AdaptPopoverWindowComponent, AdaptPopoverWindow2Component]
})
export class AdaptPopoverModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptPopoverModule> {
    return {
      ngModule: AdaptPopoverModule
    };
  }
}

export * from './popover.config';
export {AdaptPopoverDirective, AdaptPopoverWindowComponent, AdaptPopoverWindow2Component} from './popover.directive';
